import React, { useCallback, useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Card } from "primereact/card";
import { InputText } from "primereact/inputtext";
import { Button } from "primereact/button";
import { Message } from "primereact/message";
import { emailRegex } from "../../utilities/helpers";
import { shallowEqual } from "react-redux";
import { useAppDispatch, useAppSelector } from "../../utilities/hooks";
import { userActions } from "../../store/user/userSlice";
import { IAppState } from "../../models/appState";
import { IUserLogin } from "../../models/userLogin";
import { ERROR_MESSAGE_EMAIL } from "../../utilities/constants";

const Login: React.FC = () => {
  const [email, setEmail] = useState("demo@brizkcrm.com");
  const [password, setPassword] = useState("crmdemo");
  const [emailValid, setEmailValid] = useState(true);
  const [pswValid, setPswValid] = useState(true);
  const [loginValid, setLoginValid] = useState(false);
  const [inactive, setInactive] = useState(false);
  const navigate = useNavigate();
  const [isFetching, isError, isProfile, profile] = useAppSelector(
    (state: IAppState) => [
      state.users.isFetching,
      state.users.isError,
      state.users.isProfile,
      state.users.profile,
    ],
    shallowEqual
  );
  const dispatch = useAppDispatch();
  const memo = useRef(false);

  const isValidEmail = useCallback((email = "") => emailRegex.test(email), []);

  useEffect(() => {
    const isValid =
      emailValid && pswValid && email.length > 0 && password.length > 0;
    setLoginValid(isValid);
  }, [emailValid, pswValid, email, password]);

  useEffect(() => {
    if (memo.current && !isFetching && !isError && isProfile) {
      if (profile.status) {
        navigate("/welcome");
      } else {
        setInactive(true);
      }
    }
  }, [isFetching, isError, isProfile, profile, navigate]);

  const onEmailChange = (e: any) => {
    const newValue = (e.target.value || "").trim();
    const isEmailValid = isValidEmail(newValue);
    setEmailValid(isEmailValid);
    setEmail(newValue);
  };

  const onPswChange = (e: any) => {
    const newValue: string = (e.target.value || "").trim();
    const isPswValid = newValue.length > 0;
    setPswValid(isPswValid);
    setPassword(newValue);
  };

  const onClick = () => {
    memo.current = true;
    const item: IUserLogin = { email, password };
    dispatch(userActions.authUser(item));
  };

  return (
    <>
      <div style={{ height: 70 }}>&nbsp;</div>
      <div className="hidden-xs" style={{ height: 80 }}>
        &nbsp;
      </div>
      <div className="grid">
        <div className="col">&nbsp;</div>
        <div className="col">
          <h2 className="caption">Customer Relationship Management</h2>
          {(isError || inactive) && (
            <Message severity="error" text="Email/password is invalid." />
          )}
        </div>
        <div className="col">&nbsp;</div>
      </div>
      <div className="grid">
        <div className="col">&nbsp;</div>
        <div className="col">
          <Card title="BrizkCRM: Login">
            <div className="grid">
              <div className="col-12">
                <InputText
                  type="email"
                  placeholder="demo@brizkcrm.com"
                  value={email}
                  onChange={onEmailChange}
                />
                <br />
                {!emailValid && (
                  <Message severity="error" text={ERROR_MESSAGE_EMAIL} />
                )}
              </div>
              <div className="col-12">
                <InputText
                  type="password"
                  placeholder="crmdemo"
                  value={password}
                  onChange={onPswChange}
                />
                <br />
                {!pswValid && (
                  <Message severity="error" text="Password is required." />
                )}
              </div>
            </div>
            <div>
              <Button type="button" disabled={!loginValid} onClick={onClick}>
                <i className="fas fa-sign-in"></i>
                <span className="px-3">Sign in</span>
              </Button>
            </div>
          </Card>
        </div>
        <div className="col">&nbsp;</div>
      </div>
    </>
  );
};

export default Login;
